import { sample } from 'effector';
import { createField } from 'lib/createField';
export const checkboxField = ({ name, defaultChecked = false, fieldValue = '', isRequired = false, requiredErrorText = 'ÐŸÐ¾Ð»Ðµ Ð¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾ Ð´Ð»Ñ Ð·Ð°Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ñ', isBoolean = false, }) => {
    const getInitialValue = () => {
        if (defaultChecked) {
            if (isBoolean)
                return true;
            return fieldValue;
        }
        return isBoolean ? false : '';
    };
    const { type, value, isTouched, isValid, error, hasError, triggers, handlers } = createField({
        name,
        value: getInitialValue(),
    });
    value.on(handlers.onChange, (_, e) => {
        if (isBoolean)
            return e.currentTarget.checked;
        return e.currentTarget.checked ? fieldValue : '';
    });
    sample({
        source: [value, isTouched],
        clock: triggers.forceValidate,
        fn: ([value, isTouched]) => {
            if (isTouched && isRequired && !value)
                return requiredErrorText;
            return null;
        },
        target: error,
    });
    return {
        type,
        name,
        value,
        isTouched,
        isRequired,
        isValid,
        error,
        hasError,
        triggers: {
            ...triggers,
        },
        handlers: {
            ...handlers,
        },
    };
};
