import { createEvent, createStore, forward, sample } from 'effector';
const createFieldEvents = (name) => {
    const onChanged = createEvent(`${name}Changed`);
    const onFocused = createEvent(`${name}Focused`);
    const onBlurred = createEvent(`${name}Blurred`);
    const onTouched = createEvent(`${name}Touched`);
    return {
        onChanged,
        onFocused,
        onBlurred,
        onTouched,
    };
};
const createFiledTriggers = (name) => {
    const onValidate = createEvent(`${name}ValidationTriggered`);
    const onForceValidate = createEvent(`${name}ForceValidationTriggered`);
    return {
        onValidate,
        onForceValidate,
    };
};
export const createField = ({ name, value, validateOn = 'change', }) => {
    const $value = createStore(value);
    const $isTouched = createStore(false);
    const $error = createStore(null);
    const $hasError = $error.map((error) => error !== null);
    const $isValid = $value.map((value) => Boolean(value));
    const { onChanged, onBlurred, onFocused, onTouched } = createFieldEvents(name);
    const { onValidate, onForceValidate } = createFiledTriggers(name);
    $isTouched
        .on(onValidate, () => true)
        .on(onTouched, (touched) => {
        if (touched)
            return;
        return !touched;
    });
    forward({
        from: onFocused,
        to: onTouched,
    });
    forward({ from: onValidate, to: onForceValidate });
    forward({
        from: {
            change: onChanged,
            blur: onBlurred,
            focus: onFocused,
            formSubmit: createEvent(),
        }[validateOn],
        to: onForceValidate,
    });
    sample({
        source: $value,
        clock: onBlurred,
    });
    return {
        type: 'field',
        name,
        value: $value,
        error: $error,
        isTouched: $isTouched,
        hasError: $hasError,
        isValid: $isValid,
        handlers: {
            onChange: onChanged,
            onFocus: onFocused,
            onBlur: onBlurred,
        },
        triggers: {
            validate: onValidate,
            forceValidate: onForceValidate,
        },
    };
};
