import { combine, createEvent, forward } from 'effector';
const getFieldSetValidation = (fields) => {
    const validate = createEvent();
    forward({
        from: validate,
        to: fields.map((field) => field.triggers.validate),
    });
    // TODO research cases when boolean checkbox defaults to true
    // TODO look for cases to create checkbox isValid inside component
    const isValid = combine(fields.map((field) => field.isValid), (errors) => !errors.includes(true));
    return { validate, isValid };
};
const getFieldSetValues = (type, fields) => {
    if (type === 'array') {
        return fields.map((field) => field.value);
    }
    const values = {};
    fields.forEach((field) => {
        values[field.name] = field.value;
    });
    return values;
};
export const createFieldset = (name, fields, type) => {
    const { validate, isValid } = getFieldSetValidation(fields);
    const values = getFieldSetValues(type, fields);
    return {
        name,
        type: 'fieldset',
        value: combine(values),
        triggers: {
            validate,
        },
        isValid,
    };
};
